<template>
  <div>
    <b-card title="Filters">
      <b-row>
        <b-col md="4">
          <b-form-group label="Employee">
            <v-select
              v-model="filter_employee"
              :options="employees"
              placeholder="Select Employee"
              label="name"
            />
          </b-form-group>
        </b-col>
        <b-col md="4">
          <b-form-group label="Month Year">
            <month-picker-input
              v-model="month_year"
              :no-default="true"
              @input="monthYearSelected"
            />
          </b-form-group>
        </b-col>
        <b-col md="4">
          <b-form-group label="Department">
            <v-select
              v-model="filter_department"
              :options="departments"
              placeholder="Select Department"
              label="name"
            />
          </b-form-group>
        </b-col>
      </b-row>
    </b-card>

    <b-card>
      <div class="d-flex justify-content-between mb-2">
        <download-excel
          :data="exportItems"
          :fields="exportFields"
          :name="'Attendance Report'"
          :worksheet="'Attendance Report'"
          class="cursor-pointer"
        >
          <b-button
            variant="outline-secondary"
            size="sm"
            class="mr-1"
            style="padding: 0.6rem"
          >
            <feather-icon icon="FileIcon" size="20" class="text-success" />
            <label style="font-size: 14px; margin-left: 5px; cursor: pointer"
              >Export</label
            >
          </b-button>
        </download-excel>
        <b-link :to="{ name: 'hrm/employee/add' }">
          <b-button variant="primary">Add Employee</b-button>
        </b-link>
      </div>
      <div class="d-flex justify-content-between align-items-center mb-1">
        <div class="d-flex align-items-center">
          <label for="" class="mr-1" style="font-size: 14px">Sort</label>
          <b-form-select
            id="perPageSelect"
            v-model="perPage"
            size="sm"
            :options="pageOptions"
            class="w-100"
          />
        </div>
        <div class="d-flex align-items-center">
          <label for="" class="mr-1" style="font-size: 14px">Search</label>
          <b-form-input
            id="searchInput"
            v-model="filter"
            size="sm"
            type="search"
            placeholder="Search"
          />
        </div>
      </div>
      <b-row>
        <b-col cols="12">
          <b-table
            striped
            hover
            responsive
            :per-page="perPage"
            :current-page="currentPage"
            :items="items"
            :fields="fields"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            :filter="filter"
            @filtered="onFiltered"
          >
            <template #cell(employee)="data">
              <span
                v-b-modal="'detail_attendance_' + data.value"
                class="text-primary"
              >
                {{ data.value }}
              </span>
            </template>
            <template #cell(profile_photo_url)="data">
              <b-avatar :src="data.value" />
            </template>

            <template #cell(is_active)="data">
              <b-badge :variant="status[1][data.value]">
                {{ status[0][data.value] }}
              </b-badge>
            </template>
          </b-table>
        </b-col>

        <b-col cols="12">
          <div class="d-flex justify-content-between align-items-center">
            <span>
              Showing
              {{ perPage > totalRows ? totalRows : perPage }} of
              {{ totalRows }} entries
            </span>
            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              align="right"
              size="sm"
              class="my-0"
            />
          </div>
        </b-col>
      </b-row>

      <b-modal
        v-for="item in items"
        :key="item.id"
        :id="'detail_attendance_' + item.id"
        size="lg"
        hide-footer
      >
        <div class="text-center mb-3" style="line-height: 1px">
          <h1 class="mb-2">Attendance</h1>
          <span>
            This is data for employee name <b>{{ item.id }}</b>
          </span>
        </div>
      </b-modal>
    </b-card>
  </div>
</template>

<style>
.table thead th {
  white-space: nowrap;
}

.month-picker-input-container {
  width: 100% !important;
}

.month-picker-input-container .month-picker-input {
  width: 100% !important;
  padding: 0.78em;
}

.btn-outline-secondary {
  border-color: #e4e7ea;
  color: #5a8dee;
  background-color: #fff;
  border-radius: 0.25rem;
}
</style>

<script>
import ToastificationContent from "@/@core/components/toastification/ToastificationContent.vue";
import moment from "moment";
import { MonthPickerInput } from "vue-month-picker";
export default {
  components: {
    MonthPickerInput,
  },
  data() {
    return {
      filter_employee: null,
      employees: [],

      month_year: null,
      month: null,
      year: null,

      filter_department: null,
      departments: [],

      // table
      perPage: 5,
      pageOptions: [3, 5, 10],
      totalRows: 1,
      currentPage: 1,
      sortBy: "",
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      fields: [
        {
          key: "profile_photo_url",
          label: "Avatar",
        },
        {
          key: "employee",
          label: "Employee",
        },
        { key: "clock_in", label: "Clock In", sortable: true },
        { key: "late", label: "Late", sortable: true },
        { key: "working_from", label: "Working From", sortable: "true" },
        { key: "break_in", label: "Break In", sortable: "true" },
        { key: "break_out", label: "Break Out", sortable: "true" },
        { key: "break_hours", label: "Break Hours", sortable: "true" },
        { key: "clock_out", label: "Clock Out", sortable: "true" },
        {
          key: "clock_out_address",
          label: "Clock Out Address",
          sortable: true,
        },
        { key: "work_hours", label: "Work Hours", sortable: true },
        { key: "overtime", label: "Overtime", sortable: true },
        { key: "totally", label: "Totally", sortable: true },
      ],
      items: [],
      status: [
        {
          1: "Active",
          2: "Inactive",
        },
        {
          1: "light-success",
          2: "light-danger",
        },
      ],

      // export excel
      exportFields: {
        "Employee Name": "name",
        "Clock In": "clock_in",
        late: "late",
        "Working From": "working_from",
        "Break In": "break_in",
        "Break Out": "break_out",
        "Break Hours": "break_hours",
        "Clock Out": "clock_out",
        "Clock Out From": "clock_out_address",
        "Work Hours": "work_hours",
        Overtime: "overtime",
        Totally: "totally",
      },
      exportItems: [],
    };
  },
  mounted() {
    this.fetchData();
    // on one minutes update data
    setInterval(() => {
      this.fetchData();
    }, 60000);

    this.getEmployees();
    this.getDepartments();
  },
  methods: {
    fetchData() {
      this.items = [];

      this.callApi({
        method: "GET",
        url: "/attendances/fetchAll",
        success: (response) => {
          response.result.data.forEach((item) => {
            // push data to items
            this.items.push({
              id: item.id,
              employee: item.user.name,
              clock_in: moment(item.clock_in_time)
                .locale("id")
                .format("HH:mm:ss"),
              late: `${item.late != null ? item.late : 0} Hours`,
              working_from: this.truncateText(item.working_from, 20),
              break_in: moment(item.break_in_time)
                .locale("id")
                .format("HH:mm:ss"),
              break_out: moment(item.break_out_time)
                .locale("id")
                .format("HH:mm:ss"),
              break_hours: `${item.break_hours} Hours`,
              clock_out: moment(item.clock_out_time)
                .locale("id")
                .format("HH:mm:ss"),
              clock_out_address: this.truncateText(item.clock_out_addres, 20),
              work_hours: `${
                item.work_hours != null ? item.work_hours : 0
              } Hours`,
              overtime: `${item.Overtime != null ? item.Overtime : 0} Hours`,
              totally: `${item.Totally != null ? item.Totally : 0} Hours`,
            });

            this.exportItems.push({
              id: item.id,
              name: item.id,
              clock_in: moment(item.clock_in_time)
                .locale("id")
                .format("HH:mm:ss"),
              late: `${item.late != null ? item.late : 0} Hours`,
              working_from: item.working_from,
              break_in: moment(item.break_in_time)
                .locale("id")
                .format("HH:mm:ss"),
              break_out: moment(item.break_out_time)
                .locale("id")
                .format("HH:mm:ss"),
              break_hours: `${item.break_hours} Hours`,
              clock_out: moment(item.clock_out_time)
                .locale("id")
                .format("HH:mm:ss"),
              clock_out_address: item.clock_out_addres,
              work_hours: `${
                item.work_hours != null ? item.work_hours : 0
              } Hours`,
              overtime: `${item.Overtime != null ? item.Overtime : 0} Hours`,
              totally: `${item.Totally != null ? item.Totally : 0} Hours`,
            });
          });

          this.totalRows = this.items.length;
        },
        error: (error) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Failed to fetch data",
              variant: "error",
            },
          });
        },
      });
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },

    getEmployees() {
      this.callApi({
        url: "userAll",
        method: "GET",
        success: (res) => {
          this.employees = res.result.data;
        },
      });
    },

    getDepartments() {
      this.callApi({
        url: "department",
        method: "GET",
        success: (res) => {
          this.departments = res.result.data;
        },
      });
    },

    monthYearSelected() {
      this.month = this.month_year.month;
      this.year = this.month_year.year;
    },
  },
};
</script>